body, .App {
  font-family: 'Shadows Into Light', cursive;
}

.App {
  text-align: center;
}

.App-header {
  background-color: rgba(194, 130, 133, 0.9);
  height: 75px;
  padding: 10px;
  color: white;
  display: -webkit-flex; /* Safari */
  -webkit-align-items: center; /* Safari 7.0+ */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.App-title {
  font-size: 2em;
  font-weight: bolder;
  letter-spacing: 3px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.App-intro {
  font-size: large;
}

.hamburger{
  background: none;
  position: absolute;
  top: 10px;
  left: 0;
  padding: 5px 15px 0px 15px;
  color: rgb(255, 255, 255);
  border: 0;
  font-size: 3em;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
}
.cross{
  background: none;
  position: absolute;
  top: 10px;
  left: 0;
  padding: 11px 15px 0px 15px;
  color: rgb(255, 255, 255);
  border: 0;
  font-size: 7em;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
}
/* *** Sidebar *** */
#sidebar {
  background-color: rgba(190, 130, 133, 0.75);
  width: 320px;
  height: calc(100vh - 95px);
  position: absolute;
  -ms-flex-line-pack: center;
      align-content: center;
  display: block;
  border: none;
}
.infoBtn {
  width: 80px;
  height: 30px;
  font-size: 1em;
  font-weight: bolder;
  font-family: inherit;
  letter-spacing: 2px;
  color: rgb(255, 255, 255);
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: rgb(130, 194, 191);
  border: solid 1px rgb(96, 157, 154);
}
.input {
  width: 90%;
  font-size: 1.1em;
  font-family: inherit;
  margin-top: 10px;
}
.location-info {
  width: 300px;
  height: 200px;
  max-width: 90%;
  background-color: rgb(255, 255, 255);
  margin-top: 10px;
  margin-left: 10px;
  padding: 2px;
  font-size: 1em;
}
.select {
  width: 90%;
  font-family: inherit;
  background-color: rgb(255, 255, 255);
  font-size: 1em;
  margin-top: 10px;
}
hr {
  border: 0;
  height: 2px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0.01)), color-stop(rgba(130, 194, 191, 0.99)), to(rgba(255, 255, 255, 0.01)));
  background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0.01), rgba(130, 194, 191, 0.99), rgba(255, 255, 255, 0.01));
  background-image: -o-linear-gradient(left, rgba(255, 255, 255, 0.01), rgba(130, 194, 191, 0.99), rgba(255, 255, 255, 0.01));
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.01), rgba(130, 194, 191, 0.99), rgba(255, 255, 255, 0.01));
}



