img, embed, object, video {
  max-width: 100%;
}

/* *** Sidebar *** */
#sidebar {
  z-index: 100;
  max-width: 100%;
  overflow-y: scroll;
}
.locations-list-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.locations-list {
  width: 100%;
}

@media screen and (max-width: 375px) {
  .App-title {
    color: green;
    padding-left: 1.7em;
    font-size: 1.7em;
  }
  .hamburger {
    font-size: 2.5em;
    padding: 8px 15px 0px 15px;
  }
  .cross {
    font-size: 6em;
    padding: 13px 15px 0px 15px;
  }
  .visible {
    visibility: visible;
    opacity: 1;
    -o-transition: visibility 300ms linear 500ms, opacity 300ms;
    transition: visibility 300ms linear 500ms, opacity 300ms;
    -webkit-transition: visibility 300ms linear 500ms, opacity 300ms; /* Safari */
    -ms-transition: visibility 300ms linear 500ms, opacity 300ms; /* IE 9 */
  }
  #sidebar {
    -webkit-transform: translate(-320px, 0);
        -ms-transform: translate(-320px, 0);
            transform: translate(-320px, 0);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transition: transform 0.3s ease; /* Safari */
    -ms-transition: transform 0.3s ease; /* IE 9 */
  }
}

@media screen and (min-width: 376px) and (max-width: 450px) {
  .App-title {
    color: red;
    padding-left: 2em;
  }
  .hamburger {
    font-size: 2.8em;
    padding: 6px 15px 0px 15px;
  }
  .cross {
    font-size: 6.6em;
    padding: 11px 15px 0px 15px;
  }
  .visible {
    visibility: visible;
    opacity: 1;
    -o-transition: visibility 300ms linear 500ms, opacity 300ms;
    transition: visibility 300ms linear 500ms, opacity 300ms;
    -webkit-transition: visibility 300ms linear 500ms, opacity 300ms; /* Safari */
    -ms-transition: visibility 300ms linear 500ms, opacity 300ms; /* IE 9 */
  }
  #sidebar {
    -webkit-transform: translate(-320px, 0);
        -ms-transform: translate(-320px, 0);
            transform: translate(-320px, 0);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transition: transform 0.3s ease; /* Safari */
    -ms-transition: transform 0.3s ease; /* IE 9 */
  }
}

@media screen and (min-width: 451px) and (max-width: 650px) {
  .App-title {
    color: blue;
  }
  .visible {
    visibility: visible;
    opacity: 1;
    -o-transition: visibility 300ms linear 500ms, opacity 300ms;
    transition: visibility 300ms linear 500ms, opacity 300ms;
    -webkit-transition: visibility 300ms linear 500ms, opacity 300ms; /* Safari */
    -ms-transition: visibility 300ms linear 500ms, opacity 300ms; /* IE 9 */
  }
  #sidebar {
    -webkit-transform: translate(-320px, 0);
        -ms-transform: translate(-320px, 0);
            transform: translate(-320px, 0);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transition: transform 0.3s ease; /* Safari */
    -ms-transition: transform 0.3s ease; /* IE 9 */
  }
}

@media screen and (min-width: 651px) {
  .hamburger {
    visibility: hidden;
    opacity: 0;
    -o-transition: visibility 300ms linear 500ms, opacity 300ms;
    transition: visibility 300ms linear 500ms, opacity 300ms;
    -webkit-transition: visibility 300ms linear 500ms, opacity 300ms; /* Safari */
    -ms-transition: visibility 300ms linear 500ms, opacity 300ms; /* IE 9 */
  }
  #sidebar {
    -webkit-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transition: transform 0.3s ease; /* Safari */
    -ms-transition: transform 0.3s ease; /* IE 9 */
  }
}



